import { Panel, Button } from '@vkontakte/vkui';
import bridge from '@vkontakte/vk-bridge';
import '../css/intro.css';
import State from '../store/State';
import { routes } from '../types/enums';

export default ({id}: IpanelProps) => {
	return (
		<Panel id={id}>
			<div className='intro'>
				<div className='intro-header'> <br /> <b> !</b></div>
				<div className='intro-logo' />
				<div className='intro-descr'>     ,<br />  .</div>
				<div className='intro-notif'>   ,<br />    .</div>
				<div className='intro-button'>
					<Button size='m' onClick={() => {
						bridge.send('VKWebAppAllowNotifications', {}).then(res => {
							if (res.result) {
								State.setActivePanel(routes.HOME)
							}
						});
					}}>,   !</Button>
				</div>
				<div className='intro-continue' onClick={() => State.setActivePanel(routes.HOME)}></div>
			</div>
		</Panel>
	);
}